<template>
  <div class="admin-reset-pass">
    <Header :title="''"></Header>
    <el-table class="table" :data="tableData" stripe style="width: 100%">
      <el-table-column type="index" label="" width="80">
      </el-table-column>
      <el-table-column prop="name" label="" width="120">
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="{row,$index}">
          <el-button @click="reset(row.id)"></el-button>
        </template>
      </el-table-column>
      <!-- <el-table-column label="">
        <template slot-scope="{row,$index}">
          <el-input v-model="row.diff"></el-input>
        </template>
      </el-table-column> -->
    </el-table>
  </div>
</template>

<script>
  import Header from '../components/Header.vue'
  export default {
    name: 'index',
    data() {
      return {
        tableData: [],
        userInfo: '',
        ajaxFg: false
      }
    },
    created() {
      this.userInfo = JSON.parse(localStorage.getItem('userInfo'))
      this.getData()
    },
    methods: {
      reset(id) {
        if (this.ajaxFg) {
          return
        }
        this.ajaxFg = true
        let params = {
          id: id,
          password: 123
        }
        this.$http.get('http://112.74.55.229:8090/bc/passworduser.xhtml', {params: params})
        .then((res) => {
          this.ajaxFg = false
          if (res.body.code === 200) {
            this.$message('')
          } else {
            this.$message('')
          }
        })
        .catch((err) => {
          this.ajaxFg = false
          this.$message('')
        })
      },
      getData() {
        this.$http.get('http://112.74.55.229:8090/bc/getalluser.xhtml')
        .then((res) => {
          if (res.body.code === 200) {
            this.tableData = JSON.parse(res.body.data)
          } else {
            this.$message('')
          }
        })
        .catch((err) => {
          this.$message('')
          console.log(err)
        })
      }
    },
    components: {
      Header
    }
  }
</script>

<style>
.admin-reset-pass {
  margin-top: 50px;
}
@media screen and (max-width: 700px) {
  .admin-reset-pass .table {
    font-size: 12px;
  }
  .admin-reset-pass .el-button {
    font-size: 12px;
  }
}
</style>