<template>
  <div class="change-pass">
    <Header :title="''"></Header>
    <el-form class="form" ref="form" :model="form" label-width="80px">
      <!-- <el-form-item label="">
        <el-input class="item" v-model="form.password"></el-input>
      </el-form-item> -->
      <el-form-item label="">
        <el-input class="item" v-model="form.password"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input class="item" v-model="form.again"></el-input>
      </el-form-item>
      <el-form-item>
        <!-- <el-button class="button"></el-button> -->
        <el-button class="button" @click="submit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import Header from '../components/Header'
  export default {
    data() {
      return {
        form: {
          password: '',
          again: ''
        },
        groups: [{
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }],
        userNames: [{
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }, {
          value: '2',
          label: '2'
        }, {
          value: '',
          label: ''
        }],
        userInfo: {},
        ajaxFg: false
      }
    },
    created() {
      this.userInfo = JSON.parse(localStorage.getItem('userInfo'))
    },
    methods: {
      submit() {
        if (this.ajaxFg) {
          return 
        }
        this.ajaxFg = true
        if (this.form.password !== this.form.again) {
          this.$message('')
          return
        }
        let params = {
          id: this.userInfo.id,
          password: this.form.password
        }
        this.$http.get('http://112.74.55.229:8090/bc/passworduser.xhtml', {params: params})
        .then((res) => {
          this.ajaxFg = false
          if (res.body.code === 200) {
            this.$message('')
            history.go(-1)
          } else {
            this.$message('')
          }
        })
        .catch((err) => {
          this.ajaxFg = false
          this.$message('')
        })
      }
    },
    components: {
      Header
    }
  }
</script>

