            classify: item.classify
          }
          let name = this.$http.get('http://112.74.55.229:8090/bc/updateuserclassify.xhtml', {params: params})
          all.push(name)
        })
        Promise.all(all).then((res) => {
          let failFg = false
          res.map((item) => {
            if (item.body.code !== 200) {
              failFg = true
            }
          })
          if (res.length === 0) {
            failFg = true
          }
          if (res.length !== all.length) {
            failFg = true
          }
          if (failFg) {
            this.$message('')
          } else {
            this.$message('')
            history.go(-1)
          }
        })
        .catch(() => {
          this.$message('')
        })
        if (this.ajaxFg) {
          return
        }
        this.ajaxFg = true
        let params = {
          id: id,
          password: 123
        }
        this.$http.get('http://112.74.55.229:8090/bc/updateclassify.xhtml', {params: params})
        .then((res) => {
          this.ajaxFg = false
          if (res.body.code === 200) {
            this.$message('')
          } else {
            this.$message('')
          }
        })
        .catch((err) => {
          this.ajaxFg = false
          this.$message('')
        })
      },
      getData() {
        this.$http.get('http://112.74.55.229:8090/bc/getalluser.xhtml')
        .then((res) => {
